import React, { useEffect, useState } from "react";

export const Repe = ({ id, pNum, pWeigth }) => {
  const [weigth, setWeigth] = useState(0);
  const [num, setNum] = useState(0)

  useEffect(() => {
    setWeigth(pWeigth);
    setNum(pNum);
  }, [weigth, num]);

  return (
    <tr>
        <th scope="row">{ id }</th>
        <td>{pWeigth}<input value={unit} onChange={(event) => setWeigth(event.target.value)}
                                        /></td>
        <td>{pNum}</td>
        
  </tr>
  );
};